import React from "react";
import { Link, useMatch, useNavigate, useResolvedPath } from "react-router-dom";
import PropTypes from "prop-types";
import { NAVIGATIONS } from "../../constants/routes";

export const Header = () => {
  const navigate = useNavigate();
  return (
    <header className="">
      <div>
        <h3
          onClick={() => navigate("/")}
          style={{ cursor: "pointer" }}
          className="float-md-start mb-0"
        >
          P2E<span className="text-secondary">UPDATES</span>
        </h3>
        <nav className="nav nav-masthead justify-content-center float-md-end">
          {NAVIGATIONS.map(({ name, to }, index) => (
            <NavItem key={index} name={name} to={to} />
          ))}
        </nav>
      </div>
    </header>
  );
};

const NavItem = ({ name, to }) => {
  let resolved = useResolvedPath(to);
  let match = useMatch({ path: resolved.pathname + "/*", end: true });

  return (
    <Link
      to={to}
      className={`nav-link ${match && "active"}`}
      aria-current="page"
    >
      {name}
    </Link>
  );
};

NavItem.propTypes = {
  name: PropTypes.string,
  to: PropTypes.string,
};
