import React, { useState } from "react";

import { ListItem, TokenIcon, ListItemPart } from "./GameTable.style";
import { Spin, List } from "antd";
import { useNavigate } from "react-router-dom";
import { GAMES } from "../../constants/crypto";
import Title from "antd/lib/typography/Title";
// import Search from "antd/lib/input/Search";
// import { TableContainer } from "../../App.style";

// var formatter = new Intl.NumberFormat("en-US", {
//   style: "currency",
//   currency: "PHP",
// });

export const GameTable = () => {
  const [state] = useState({ games: [], loading: false });
  const navigate = useNavigate();
  const { loading } = state;

  const handleOnClick = (game) => () => {
    // TODO: Add GA
    if (game.abbr === "PVU") {
      // window.location.href = "https://pvu.zone";
    }
    navigate(`/games/${game.abbr}`);
  };
  return (
    <Spin spinning={loading} tip="Loading...">
      {/* <Search style={{ marginBottom: 20 }} /> */}

      <List
        size="large"
        bordered
        dataSource={GAMES}
        renderItem={(token) => (
          <ListItem onClick={handleOnClick(token)}>
            <ListItemPart>
              <TokenIcon width={20} src={token.icon} />{" "}
              <Title style={{ marginBottom: 0 }} level={5}>
                {token.name}
              </Title>
            </ListItemPart>
          </ListItem>
        )}
      />
    </Spin>
  );
};

export default GameTable;
