import axios from "axios";
import { values } from "lodash";

export const getCGTokens = async (games) => {
  console.log("HERE");
  const tokens = getTOKENSofGames(games);
  const { data } = await axios.get(
    `https://api.coingecko.com/api/v3/simple/price?ids=${tokens
      .map((t) => t.id)
      .join(
        ","
      )}&vs_currencies=php,usd&include_24hr_change=true&include_market_cap=true`
  );
  const parsedToken = tokens.map((token) => ({ ...token, ...data[token.id] }));
  console.log("RESULT:", parsedToken);
  console.log("Result Values:", values(parsedToken));
  return parsedToken;
};

export const getTOKENSofGames = (games) => {
  return games.reduce((prev, curr) => {
    return [...prev, ...curr.tokens.map((t) => ({ ...t, game: curr }))];
  }, []);
};

export const formatCurrency = (val, currency = "php") => {
  var formatter = new Intl.NumberFormat("en-US", {
    style: "currency",
    currency,
  });

  return val > 1 ? formatter.format(val) : `${val}`;
};
