import React, { useEffect, useState } from "react";
import { formatCurrency, getCGTokens } from "../../helpers";
import orderBy from "lodash/orderBy";
import {
  ListItem,
  ListItemDivider,
  ListItemHeader,
  TextPercentage,
  TokenIcon,
} from "./TokenTable.style";
import { Spin, List } from "antd";
import { ListItemPart } from "./TokenTable.style";
import { useNavigate } from "react-router-dom";
import { GAMES } from "../../constants/crypto";
import Logo from "../common/Logo/Logo";

export const TokenTable = () => {
  const [state, setState] = useState({ tokens: [], loading: true });
  const navigate = useNavigate();
  const { tokens, loading } = state;
  useEffect(() => {
    initTable();
  }, []);
  const initTable = async () => {
    try {
      const result = await getCGTokens(GAMES);
      setState((prev) => ({ ...prev, loading: false, tokens: result }));
    } catch (error) {
      setState((prev) => ({ ...prev, loading: false, tokens: [] }));
    }
  };

  const handleOnClick = (game) => () => {
    // TODO: Add GA
    if (game.abbr === "PVU") {
      // window.location.href = "https://pvu.zone";
    }
    navigate(`/games/${game.abbr}`);
  };
  return (
    <Spin spinning={loading} tip="Loading...">
      {/* <Search style={{ marginBottom: 20 }} /> */}

      <List
        size="small"
        header={
          <ListItemHeader>
            <Logo />
            <ListItemPart>Cryptocurrency</ListItemPart>
            <ListItemPart>Today</ListItemPart>
            <ListItemPart align="right" width="100px">
              24Hrs
            </ListItemPart>
          </ListItemHeader>
        }
        footer={<Logo />}
        bordered
        dataSource={orderBy(tokens, ["php_24h_change"], ["desc"])}
        renderItem={(token) => (
          <ListItem onClick={handleOnClick(token.game)}>
            <ListItemDivider>
              <ListItemPart className="name">
                <TokenIcon width={14} src={token.icon} /> {token.name}{" "}
                <span style={{ marginLeft: 5 }} className="text-secondary">
                  {token.abbr}
                </span>
              </ListItemPart>
              <ListItemPart>
                {formatCurrency(token.php)}

                <span className="text-secondary">(${token.usd})</span>
              </ListItemPart>
            </ListItemDivider>
            <ListItemPart
              style={{ justifyContent: "end" }}
              align="right"
              width="69px"
            >
              <TextPercentage positive={token.php_24h_change > 0}>
                {token.php_24h_change > 0 && "+"}
                {token.php_24h_change.toFixed(2)}%
              </TextPercentage>
            </ListItemPart>
          </ListItem>
        )}
      />
    </Spin>
  );
};
